.dashboard-container {
  padding: 0;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  overflow-y: auto;
  overflow-x: hidden;
}

// ===== 헤더 =====
.dashboard-header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 32px 40px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 24px;

  .header-left {
    flex: 1;

    .welcome-section {
      h1 {
        margin: 0 0 8px 0;
        font-size: 32px;
        font-weight: 700;
        color: #2d3748;
        line-height: 1.3;
        display: flex;
        align-items: center;
        gap: 8px;
        flex-wrap: wrap;

        .greeting {
          color: #718096;
          font-weight: 400;
          font-size: 28px;
        }

        .username-highlight {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          font-weight: 800;
        }
      }

      .subtitle {
        margin: 0;
        font-size: 16px;
        color: #718096;
        font-weight: 400;
      }
    }
  }

  .header-actions {
    display: flex;
    align-items: center;
    gap: 12px;
    flex-wrap: wrap;

    .action-btn {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 12px 24px;
      border-radius: 12px;
      font-weight: 600;
      font-size: 14px;
      transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
      }

      &:active {
        transform: translateY(0);
      }

      &.viral-btn {
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        color: white;
        border: none;

        &:hover {
          background: linear-gradient(135deg, #5568d3 0%, #6a3f8f 100%);
        }
      }

      &.refresh-btn {
        background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
        color: white;
        border: none;

        &:hover {
          background: linear-gradient(135deg, #3d8fd1 0%, #00d4e0 100%);
        }
      }

      &.logout-btn {
        background: white;
        color: #e53e3e;
        border: 2px solid #e53e3e;

        &:hover {
          background: #e53e3e;
          color: white;
        }
      }
    }
  }
}

// ===== 그리드 =====
.dashboard-grid {
  padding: 32px 40px;
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
}

.dashboard-row {
  width: 100%;
  margin-bottom: 24px;
  animation: fadeInUp 0.6s ease-out;

  &:last-child {
    margin-bottom: 0;
  }
}

.dashboard-column {
  min-height: 200px;
  position: relative;

  .widget-wrapper {
    height: 100%;
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding: 24px;
    overflow: visible;
    transition: all 0.3s ease;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);

    // Hover 효과 - 소설 모듈 스타일 그대로
    &:hover {
      transform: translateY(-6px);
      box-shadow: 0 8px 24px rgba(102, 126, 234, 0.15);
      border-color: #667eea;
    }

    // Active 효과
    &:active {
      transform: translateY(-4px);
    }

    // 위젯 내부 콘텐츠
    ::ng-deep > * {
      position: relative;
      z-index: 1;
    }

    // 아이콘 애니메이션
    ::ng-deep {
      .widget-icon,
      mat-icon,
      svg {
        transition: all 0.3s ease;
      }
    }

    &:hover ::ng-deep {
      .widget-icon,
      mat-icon:not(.header-actions mat-icon),
      svg:not(.header-actions svg) {
        transform: scale(1.15) rotate(5deg);
      }
    }
  }
}

// ===== 애니메이션 =====
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// ===== 반응형 디자인 =====
@media (max-width: 1200px) {
  .dashboard-grid {
    padding: 24px;
  }

  .dashboard-header {
    padding: 24px;
  }
}

@media (max-width: 768px) {
  .dashboard-container {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }

  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    gap: 16px;

    .header-left {
      width: 100%;

      .welcome-section h1 {
        font-size: 24px;

        .greeting {
          font-size: 20px;
        }
      }

      .subtitle {
        font-size: 14px;
      }
    }

    .header-actions {
      width: 100%;
      justify-content: flex-start;

      .action-btn {
        flex: 1;
        justify-content: center;
        min-width: auto;

        span {
          display: none;
        }

        mat-icon {
          margin: 0;
        }
      }
    }
  }

  .dashboard-grid {
    padding: 16px;
  }

  .dashboard-row {
    flex-direction: column !important;
    gap: 16px !important;
    margin-bottom: 16px;
  }

  .dashboard-column {
    width: 100% !important;
    flex: none !important;
  }
}

@media (max-width: 480px) {
  .dashboard-header {
    padding: 16px;

    .header-left .welcome-section h1 {
      font-size: 20px;

      .greeting {
        font-size: 18px;
      }
    }

    .header-actions {
      gap: 8px;

      .action-btn {
        padding: 10px 16px;
        font-size: 12px;
      }
    }
  }
}
